<template>
  <div class="chat-card-root">
    <van-image
      :src="chat.avatar"
      width="100%"
      height="100%"
      lazy-load
      fit="cover"
    />
    <div class="chat-card-status offline" v-if="chat.chatStatus === 0">
      Offline
    </div>
    <div class="chat-card-status online" v-if="chat.chatStatus === 1">
      Online
    </div>
    <div class="chat-card-status busy" v-if="chat.chatStatus === 2">Busy</div>
    <div class="chat-card-bottom">
      <div class="info">
        <div class="name">{{ chat.nickname }}</div>
        <div class="price">
          {{ chat.preMinuteDiamond }}
          <van-icon :name="require('@/assets/icon/star-icon.png')" />/min
        </div>

        <div class="age">
          <van-icon
            :name="require('@/assets/icon/famale-icon.png')"
            color="#fff"
            size="12"
          />
          {{ chat.age }}
        </div>
      </div>
      <div class="video-call">
        <!-- <van-button
        class="video-button"
          :icon="require('@/assets/icon/video-open-icon.png')"
          color="#f45d82;"
          @click="connect(chat)" ></van-button> -->
        <img
          class="video-button"
          :src="require('@/assets/icon/video-button.png')"
          @click="connect(chat)"
        />
      </div>
    </div>
    <Connect
      :uid="curHost.uid"
      :poster="curHost.avatar"
      @close="closeConnect"
      :key="Date.now()"
      v-if="connectShow"
    />
    <div class="recharge-tips-modal" v-if="showRechargeTips">
      <div class="to-recharge-dialog">
        <div class="close" @click="showRechargeTips = false">
          <van-icon name="close" />
        </div>
        <h2>You coins are running out</h2>
        <p>To continue video call</p>
        <div class="button">
          <div @click="rechargeNow">Recharge now</div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import Vue from "vue";
import { Image, Lazyload } from "vant";
import Connect from "../components/Connect";
import * as Server from "../server";
import { mapState, mapMutations } from "vuex";

Vue.use(Lazyload).use(Image);
export default {
  name: "chat-card",
  components: {
    Connect
  },
  data() {
    return {
      curHost: null,
      connectShow: false,
      showRechargeTips: false
    };
  },
  props: {
    chat: {
      type: Object,
      default: function() {
        return {};
      }
    }
  },
  computed: {
    ...mapState(["profile", "inDebug"])
  },
  methods: {
    ...mapMutations(["updateProfile"]),
    async loadProfile() {
      let { data, status } = await Server.Profile();
      if (status !== 0) {
        console.log("LoadProfile Failed => ", data);
        return;
      }
      this.updateProfile(data);
      localStorage.setItem("profile", JSON.stringify(data));
    },
    async connect(info) {
      let url = window.location.href;
      if (/inTest/gi.test(url)) info.uid = 3301603086703212;
      if(this.inDebug){
        info.uid = 3301603086703212;  
      }
      console.log("Connect => ", info.uid, " => ", info);

      // if(!localStorage.getItem("noFirstUse")){
      //   console.log("IS New User, Free first call.");
      // }else{
      //   await this.loadProfile();
      //   console.log("Connect loadProfile => ", this.profile);
      //   if (!this.profile.diamond || this.profile.diamond < 100) {
      //     this.$toast("The balance is insufficient, please recharge");
      //     this.$router.push("/recharge");
      //     return;
      //   }
      // }
      await this.loadProfile();
      console.log("Connect loadProfile => ", this.profile);
      if (!this.profile.diamond || this.profile.diamond < 100) {
        this.$toast("The balance is insufficient, please recharge");
        this.$router.push("/recharge");
        return;
      }
      this.curHost = info;
      this.connectShow = true;
    },
    async closeConnect(){
      this.connectShow = false;
      if(!localStorage.getItem("noFirstUse")) {
        console.log("CloseConnect => ", "isFirstUse");
        return;
      }
      let { status, data } = await Server.QueryBalance(this.profile.uid);
      console.log("CloseConnect => ", status, data);
      if(status !== 0) return;
      if(data < 100){
        console.log("no balance and noFirstUse");
        this.showRechargeTips = true;
      }
    },
    rechargeNow(){
      this.$router.push("/recharge");
      this.showRechargeTips = false;
    }
  }
};
</script>

<style lang="less">
.chat-card-root {
  width: 100%;
  height: 220px;
  border-radius: 8px;
  background-color: #ddd;
  position: relative;
  overflow: hidden;

  * {
    color: #000 im !important;
  }

  .chat-card-status {
    position: absolute;
    right: 5px;
    top: 8px;
    height: 22px;
    display: flex;
    align-items: center;
    padding: 0 8px;
    font-size: 14px;
    font-weight: 400;
    color: #fff;
    background-color: rgba(0, 0, 0, 0.1);
    border-radius: 4px;

    &::before {
      content: "";
      width: 12px;
      height: 12px;
      border-radius: 50%;
      background: #ddd;
      margin-right: 5px;
    }
    &.online::before {
      background-color: #40dc29;
    }
    &.busy::before {
      background-color: #ffaa0f;
    }
  }
  .chat-card-bottom {
    box-sizing: border-box;
    position: absolute;
    bottom: 0;
    left: 0;
    height: auto;
    width: 100%;
    padding: 16px 5px;
    display: grid;
    grid-template-columns: auto 50px;
    gap: 5px;
    background: linear-gradient(
      180deg,
      rgba(167, 167, 167, 0) 0%,
      rgba(0, 0, 0, 1) 100%
    );

    .info {
      display: flex;
      flex-direction: column;
      align-items: flex-start;
    }
    .name {
      font-size: 15px;
      color: #fff;
      font-weight: bold;
      line-height: 29px;
      height: 29px;
      overflow: hidden;
    }
    .age {
      background-color: #f45d82;
      font-size: 12px;
      font-weight: 300;
      display: flex;
      align-items: center;
      justify-content: center;
      color: #fff;
      height: 16px;
      padding: 0 8px;
      border-radius: 4px;
      // margin-left: 10px;
    }

    .price {
      font-size: 12px;
      font-weight: 400;
      color: #fff;
      line-height: 20px;
      display: flex;
      align-items: center;
    }

    .video-call {
      text-align: right;
      vertical-align: bottom;
    }

    .video-button {
      width: 50px;
      height: 50px;
    }
  }
  
  .recharge-tips-modal {
    position: fixed;
    z-index: 99999999;
    width: 100vw;
    height: 100vh;
    left: 0;
    top: 0;
    background-color: rgba(0, 0, 0, 0.45);
    display: flex;
    align-items: center;
    justify-content: center;

    .to-recharge-dialog {
      width: 80%;
      padding: 10px;
      border-radius: 5px;
      background: #FFF;

      .close {
        text-align: left;
        height: 20px;
        width: 20px;
      }

      h2{
        font-size: 20px;
        font-weight: 700;
        color: #F00;
        margin: 0 0 10px 0;
        text-align: center;
      }
      p{
        font-size: 18px;
        font-weight: 500;
        color: #000;
        margin: 0 0 10px 0;
        text-align: center;
      }

      .button {
        width: 100%;
        display: grid;

        div{
          font-size: 16px;
          font-weight: 700;
          color: #FFF;
          margin: auto;
          background: linear-gradient(to right, rgb(255, 111, 0), rgb(252, 1, 97));
          line-height: 36px;
          border-radius: 40px;
          padding: 0 30px;
          margin: auto;
        }
      }
    }
  }
}
</style>
